import React, { useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";
import { useQueryClient } from "react-query";
import { useDispatch } from "react-redux";
import { useTypedSelector } from "../../hooks/useTypedSelector";
import { IdentityInterface } from "../../store/reducers/ids-reducer";
import { searchChangeTargetRefAction } from "../../store/reducers/search-reducer";
import { isFilterMatching } from "../../tools/isFilterMatching";
import { ItemEntity } from "../item-entity/ItemEntity";
import "./ListIds.css";

export const ListIds:React.FC = () => {
    const containerRef = useRef(null);
    const dispatch = useDispatch();
    const ids = useQueryClient().getQueryData("identities") as IdentityInterface[]|null;
    const filterState = useTypedSelector(state => state.filterReducer);
    const searchState = useTypedSelector(state => state.searchReducer);
    const {t} = useTranslation();

    useEffect(()=>{
        searchChangeTargetRefAction(dispatch,containerRef)
    },[])
    const idsNew:React.ReactNode[] = [];
    const idsList = ids?.reduceRight((acc:React.ReactNode[],item:IdentityInterface)=>{
        if (!isFilterMatching(filterState,searchState,item)) return acc;
        if(!!(+item.isNew)) 
            idsNew.push(<ItemEntity entity={item} key={item.imgUrl}/>);
        else 
            acc.push(<ItemEntity entity={item} key={item.imgUrl}/>);
        return acc;
    },[])

    return (
        <section ref={containerRef} className={"list-ids"}>
        <h2>{`${t("ListIds.header")} (${(idsList?.length || 0) + idsNew.length})`}</h2>
        {
            !!((idsList?.length || 0) + idsNew.length) ? <div className={"list-ids-content"} >
                {idsNew}
                {idsList}
            </div>
            : <p>
                {t("ListIds.empty")}
            </p>
        }
        </section>
        
    )
}